import React, {PureComponent} from 'react';
import Day from './day';
import Today from './todaysWthr';
import C from '../animated/celsius.svg'
import F from '../animated/fahrenheit.svg'

class DaysList extends PureComponent {
    constructor(props) {
        super(props);
        this.header = React.createRef();
  
    } 

    componentDidMount(){
        this.header.current.scrollIntoView({ behavior: "smooth" ,block:'start'});
    }

    render(){
        let fehr=true
        function setfehr() {
            fehr=!fehr
        }
    
        this.props.daysWeather.map(day => {
            day.main.temp = Math.floor((day.main.temp - 273.15) * 9 / 5 + 32)
            day.main.temp_max = Math.floor((day.main.temp_max - 273.15) * 9 / 5 + 32)
            day.main.temp_min = Math.floor((day.main.temp_min - 273.15) * 9 / 5 + 32)
      
            return day
          })
      
    
        let days = [];
        let obj = [];
        let date = this.props.daysWeather[0].dt_txt.slice(8, 11)
        for (let i = 0; i < this.props.daysWeather.length; i++) {
            if (this.props.daysWeather[i].dt_txt.slice(8, 11) === date) {
                obj.push(this.props.daysWeather[i])
            }
            else {
                days.push(obj)
                obj = []
                date = this.props.daysWeather[i].dt_txt.slice(8, 11)
                obj.push(this.props.daysWeather[i])
            }
        }
        days.push(obj)
    
    
    
    
        days.map(day => (
            day.map(d => {
                if (fehr) {
                    d.main.temp = Math.round((d.main.temp - 32) * 5 / 9)
                    d.main.temp_max = Math.round((d.main.temp_max - 32) * 5 / 9)
                    d.main.temp_min = Math.round((d.main.temp_min - 32) * 5 / 9)
                }
                else {
                    d.main.temp = Math.round((d.main.temp * 9 / 5 + 32))
                    d.main.temp_max = Math.round((d.main.temp_max * 9 / 5 + 32))
                    d.main.temp_min = Math.round((d.main.temp_min * 9 / 5 + 32))
                }
    
                return (d)
            })
        ))
    
    
    
    /// scroll to header
        const scrollToBottom = () => {
            this.ref.current.scrollIntoView({ behavior: "smooth" ,block:'start'});
        }
    
    return (
        <>
            <div className='states'>
                <div className='header' ref={this.header}>
                    <div>
                        <h1>{this.props.country.name}</h1>
                        <h3>{days[0][0].dt_txt.slice(5, 10)}</h3>
                    </div>

                    <div>
                        <button onClick={() => (setfehr(!fehr))}><img src={fehr ? F : C} className='subSvg' /></button>
                    </div>
                </div>
                <Today today={days[0]} country={this.props.country} />
                {days.slice(1).map(day => (<Day details={day} key={day.dt}/>))}
            </div>
        </>
    )}
}
export default DaysList;