import Gps from '../animated/gps.svg'
function CitySearchBar({ findCity, citySearch, getLocation ,forwardedRef}) {
    return (
        <form onSubmit={findCity}>
            <div>
                <input type='text' onChange={citySearch} placeholder='country name' ref={forwardedRef} />
                <input type='submit' />
                <button onClick={getLocation}><img src={Gps} className='subSvg' /></button>
            </div>
        </form>
    )

}
export default CitySearchBar;