import { useState, useEffect } from 'react';
import axios from 'axios';

const useFetch = (url) => {

  const [data, setdata] = useState(null)
  const [err, seterr] = useState(null)
  const [isLoading, setisLoading] = useState(null)

  useEffect(() => {
    setisLoading(true)

    if (!url) {
      setisLoading(true)
      seterr(null)
      setdata(null)
    }
    try {
      axios.get(url)
        .then(data => {
          setisLoading(false)
          seterr(null)
          setdata(data)
        })
        .catch((error) => {
          setisLoading(false)
          seterr(error)
          // Code for handling the error
        })
    } catch (error) {
      setisLoading(false)
      seterr(error)
    }
  }, [url])




  return ({ data, err, isLoading });

}
export default useFetch;
