import S01d from '../animated/01d.svg'
import S01n from '../animated/01n.svg'
import S02d from '../animated/02d.svg'
import S02n from '../animated/02n.svg'
import S03d from '../animated/03d.svg'
import S03n from '../animated/03n.svg'
import S04d from '../animated/04d.svg'
import S04n from '../animated/04n.svg'
import S09d from '../animated/09d.svg'
import S09n from '../animated/09n.svg'
import S10d from '../animated/10d.svg'
import S10n from '../animated/10n.svg'
import S11d from '../animated/11d.svg'
import S11n from '../animated/11n.svg'
import S13d from '../animated/13d.svg'
import S13n from '../animated/13n.svg'
import S50d from '../animated/50d.svg'
import S50n from '../animated/50n.svg'




function todaysWthr({ today }) {
    const svgState = {
        S01d: S01d,
        S01n: S01n,
        S02d: S02d,
        S02n: S02n,
        S03d: S03d,
        S03n: S03n,
        S04d: S04d,
        S04n: S04n,
        S09d: S09d,
        S09n: S09n,
        S10d: S10d,
        S10n: S10n,
        S11d: S11d,
        S11n: S11n,
        S13d: S13d,
        S13n: S13n,
        S50d: S50d,
        S50n: S50n
    }
    function setSvg(hour) {
        let e = '';
        for (const svg in svgState) {
            if (svgState[svg].slice(14, 17) === hour.weather[0].icon)
                e = svgState[svg]
        }
        return e
    }
    return (
        <>
            <div className='today'>
                {today.map(hour => (
                    <div className='dayCard'>
                        <img src={setSvg(hour)} />

                        <div className='info'>
                            <div>
                                <h3>{hour.main.temp}&deg;</h3>
                                <h4>{hour.weather[0].main}</h4>
                                <p>{hour.dt_txt.slice(10, 16)}</p>
                            </div>
                            <div className='subInfo'>
                                <p>{hour.main.temp_max}&deg; / {hour.main.temp_min}&deg;</p>
                                <p>Humidity: {hour.main.humidity}%</p>
                                <p>wind: {hour.wind.speed}km/h</p>
                            </div>
                        </div>
                    </div>
                ))}
            </div>
        </>
    )

}
export default todaysWthr;